<template>
        <div id="slidePanel" class="slide-panel ol-unselectable ol-control">
            {{child_msg}}
            <div class="slide-show">
                <carousel-container
                    @prev="prev"
                    @next="next"
                >
                    <carousel-slide v-for="(slide,index) in settings.slides"
                        :key="slide"
                        :index="index"
                        :visibleSlide="visibleSlide"
                        :direction="direction"
                        >
                        <div class="slide-title">{{slide.title}}</div>
                        <div class="slide-description">{{slide.description}}</div>
                        <img :src="'./src/assets/img/slides/'+slide.src" :alt="index">
                    </carousel-slide>
                </carousel-container>
            </div>
            <div class="slide-current"></div>
        </div>
</template>

<script>
// Import slide-show components
import CarouselContainer from './slideShow/CarouselContainer.vue'
import CarouselSlide from './slideShow/CarouselSlide.vue'

export default {
    name: 'Slide',
	props:{
		child_msg: {
			type: String,
			default: 'Slide Panel',
		},
        settings: {
            type: Object,
        }
	},
    data () {
        return {
            visibleSlide: 0,
            direction: '',
        }
    },
    computed: {
        slidesLen() {
            return this.settings.slides.length;
        }
    },
    methods: {
        prev () {
            if(this.visibleSlide <= 0){
                this.visibleSlide = this.slidesLen - 1;
            } else {
                this.visibleSlide--;
            }
            this.direction = 'right';
        },
        next () {
            if(this.visibleSlide >= this.slidesLen - 1 ){
                this.visibleSlide = 0;
            } else {
                this.visibleSlide++;
            }
            this.direction = 'left';
        }
    },
    mounted() {
    },
    components: { 
        CarouselContainer,
        CarouselSlide,
    },
}
</script>

<style lang="scss" scoped>
@import "@/assets/sass/style.scss";

.slide-panel{
    display:inline-block;
    justify-content: center;
    position: absolute;
    bottom: 25vh;
    left: 25vw;
    width: 50vw;
    height: 50vh;
    z-index: 1;
    border:$border-rad;
    img {
        width: 100%;
        height: 100%;
        border:$border-rad;
    }
}

.slide-title{
    height: 10%;
    width: 100%;
    text-align: center;
}

.slide-show{
    display:flex;
    width: 100%;
    height: 90%;
}

.slide-current{
    width: 100%;
    height: 10%;
}
</style>