<template>
        <div id="timePanel" class="time-panel ol-unselectable ol-control">
            <p>{{child_msg}}</p>
            <div class="time-container">
                <div class="time-header">{{current_layer}} - {{current_date}}</div>
                <div class="time-controls">
                    <div class="time-controls-dates">
                        <select name="" id="">
                            <option value="1">Select WQP...</option>
                        </select>
                    </div>
                    <div class="time-slider">
                        <input type="range" name="" id="">
                    </div>
                    <div id="time-controls-backward" class="time-controls_item ol-unselectable ol-control">
                        <button><i class="bi bi-skip-backward-circle"></i></button>
                    </div>  
                    <div id="time-controls-play" class="time-controls_item ol-unselectable ol-control">
                        <div><button><i class="bi bi-play-circle"></i></button></div>
                        <!-- <div><button><i class="bi bi-pause-circle"></i></button></div> -->
                    </div> 
                    <div id="time-controls-forward" class="time-controls_item ol-unselectable ol-control">
                        <button><i class="bi bi-skip-forward-circle"></i></button>
                    </div>
                </div>                
            </div>

        </div>
</template>

<script>
export default {
    name: 'Time',
	props:{
		child_msg: {
			type: String,
			default: 'Time Panel',
		},
        current_layer: {
			type: String,
			default: 'Select a WQP map',
		},
        current_date: {
			type: String,
			default: 'Date',
		}
	},
    data () {
        return {
        }
    }
}
</script>