<template>
        <div id="metadataPanel" class="metadata-panel ol-unselectable ol-control">
            {{child_msg}}
            <div class="meta-title">
                <h6>{{title_layer}}</h6>
            </div>
            <div class="meta-abstract">Abstract: {{abstract_layer}}</div>
            <div class="meta-info"> 
                <div class="meta-details">
                    <div class="meta-details-typology">Typology: {{typology_layer}}</div>
                    <div class="meta-details-crs">CRS: {{crs_layer}}</div>
                    <div class="meta-details-tstart">Time Start: {{tstart_layer}}</div>
                    <div class="meta-details-tend">Time end: {{tend_layer}}</div>
                </div>
                <div class="meta-legend">Legend: {{legend_layer}}</div>
            </div>
        </div>
</template>

<script>
export default {
    name: 'Layer',
	props:{
		child_msg: {
			type: String,
			default: 'Metadata Panel',
		},
        title_layer: {
			type: String,
			default: 'Title',
		},
        abstract_layer: {
			type: String,
			default: 'Select a layer',
		},
        typology_layer: {
			type: String,
			default: 'N/A',
		},
        crs_layer: {
			type: String,
			default: 'N/A',
		},
        tstart_layer: {
			type: String,
			default: 'N/A',
		},
        tend_layer: {
			type: String,
			default: 'N/A',
		},
        legend_layer: {
			type: String,
			default: 'N/A',
		},
	},
    data () {
        return {
        }
    }
}
</script>